import Vue from 'vue'
import VueTinymce from 'w-vue-tinymce-editor'
console.log(Vue)
Vue.use(VueTinymce)

const TestApp = resolve => require(['./TestApp.vue'], resolve)
const MainApp = resolve => require(['./MainApp.vue'], resolve)

const routes = [
    {
        path: '/module-a/',
        component: MainApp
    },
    {
        path: '/module-a/test',
        component: TestApp
    }
]

window.appData = window.appData || {};
window.appData.router && window.appData.router.addRoutes(routes, {
  override: true
});
